* {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

html {
  font-size: 10px;
}
@media (max-width: 640px) {
  html {
    font-size: 9px;
  }
}
@media (max-width: 450px) {
  html {
    font-size: 7px;
  }
}

.container {
  width: 1440px;
}
@media (max-width: 1250px) {
  .container {
    width: 100vw;
  }
}
@media (max-width: 950px) {
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.container .module {
  display: flex;
  justify-content: space-evenly;
  background: #FFFFFF;
  margin: 96px auto 0 auto;
}
@media (max-width: 950px) {
  .container .module {
    flex-direction: column;
    justify-content: center;
    margin: 20px;
  }
}
.container .module__overlay {
  display: none;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #121212;
  opacity: 0.5;
}
.container .module .alert {
  display: none;
  position: absolute;
  width: 920px;
  height: 210px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #FFFFFF;
  opacity: 1;
  padding: 56px 95px;
}
@media (max-width: 950px) {
  .container .module .alert {
    width: 500px;
  }
}
@media (max-width: 640px) {
  .container .module .alert {
    width: 340px;
    height: 250px;
  }
}
@media (max-width: 450px) {
  .container .module .alert {
    width: 300px;
  }
}
.container .module .alert__close {
  position: absolute;
  top: 46px;
  right: 46px;
  font-size: 1.2rem;
  cursor: pointer;
}
@media (max-width: 640px) {
  .container .module .alert__close {
    font-size: 2rem;
    top: 20px;
    right: 20px;
  }
}
.container .module .alert__title {
  font-size: 4.2rem;
  font-weight: 700;
  color: #121212;
  margin: 0;
}
.container .module .alert__text {
  font-size: 1.6rem;
  font-weight: 300;
  color: #121212;
  line-height: 150%;
}
.container .module .alert__important {
  font-weight: 700;
}
.container .module .alert__btn {
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 100%;
  color: #FFFFFF;
  text-transform: capitalize;
  padding: 16px 40px;
  border-radius: 32px;
  border: none;
  cursor: pointer;
  display: none;
  background: red;
  margin-bottom: 5px;
}
.container .module .img-hero {
  width: 505px;
  height: 330px;
  background: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_4_3,c_fill,g_auto__c_scale,w_538.jpg") center;
  background-size: cover;
}
@media (max-width: 500px) {
  .container .module .img-hero {
    width: 450px;
    background: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_1_1,c_fill,g_auto__c_scale,w_450.jpg") center;
  }
}
@media (max-width: 450px) {
  .container .module .img-hero {
    width: 200px;
    height: 200px;
    display: block;
    margin: 0 auto;
    background: url("../../images/sean-o-KMn4VEeEPR8-unsplash_1_s6zmfh_ar_1_1,c_fill,g_auto__c_scale,w_200.jpg") center;
  }
}
.container .module .content {
  width: 380px;
  height: 250px;
}
@media (max-width: 450px) {
  .container .module .content {
    width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
.container .module .content__title {
  font-size: 4.2rem;
  font-weight: 700;
  color: #121212;
  width: 380px;
  height: 50px;
  line-height: 120%;
}
@media (max-width: 450px) {
  .container .module .content__title {
    text-align: center;
  }
}
.container .module .content__text {
  font-size: 1.6rem;
  font-weight: 300;
  color: #121212;
  line-height: 150%;
}
.container .module .content__btn {
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 100%;
  color: #FFFFFF;
  text-transform: capitalize;
  padding: 16px 40px;
  border-radius: 32px;
  border: none;
  cursor: pointer;
  background: #E4C1AE;
}
@media (max-width: 500px) {
  .container .module .content__btn {
    margin: 0 auto;
  }
}
.container .module .active {
  display: block;
}